<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>{{ title }}</h1>
      <div class="chartWrap"></div>
    </section>
    <div class="centerWrap">
      <div class="center">
        <span class="title"
          >{{ accountType == 2 ? "AE +971 " : "" }} {{ username }}
          <a href="javascript:void(0)" @click="$router.go('-1')" v-if="type!='codMobile'"
            >(Change)</a
          ></span
        >
        <p>
          A text with a code has been sent to the number above, please enter the
          code below.
        </p>
        <div class="emailWrap">
          <img src="../common/images/orders.png" />
          <van-field v-model="validateCode" placeholder="Enter Verify Code" type="number" clearable />
        </div>
     
        <van-button type="danger" @click="submitSms">{{ btnText }}</van-button>
        <span class="again"
          >Didn't receive the code ?
          <a href="javascript:void(0)" @click="sendSms">(Resend Code)</a></span
        >
      </div>
    </div>
  </div>
</template>

<script>
import { sendSms, sendEmail, verifyCode } from "@/api/msg";
import { updateEmail, updateTelephone } from "@/api/user";
import {codPay} from '@/api/order'
import {validateCode} from "@/utils/validate";
export default {
  name: "verifyMobile",
  data() {
    return {
      btnText: "",
      title: "",
      username: this.$route.query.username,
      type: this.$route.query.type,
      accountType: this.$route.query.accountType,
      validateCode: ""
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      switch (this.type) {
            case "forget":
            case "register":
            case "updateEmail":
            case "reMobile":
            case "codMobile":
               this.btnText = "Verify";
              break;
            default:
             this.btnText = "Sign In";
              break;
          }
      if (this.accountType == 1) {
        this.title = "Verify Email";
      } else {
        this.title = "Verify mobile number";
      }
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    submitSms() {
      if(this.validateCode == '' || this.validateCode == undefined){
          this.$toast(" Verification code cannot be empty");
          return false;
        }
        if(!validateCode(this.validateCode)){
          this.$toast(" Verification code must be 6 digits");
          return false;
        }
      // 验证
      if (this.type == "updateEmail" || this.type == "reMobile"||this.type=='codMobile') {
        switch (this.type) {
          case "updateEmail":
            updateEmail({
              email: this.username,
              verificationCode: this.validateCode
            }).then(res => {
              this.$toast(" Add Email successfully");
              this.$router.go(-2);
            });
            break;
          case "reMobile":
            updateTelephone({
              telephone: this.username,
              verificationCode: this.validateCode
            }).then(res => {
              this.$toast(" Add Mobile successfully");
              this.$router.go(-2);
            });
            break;
              case "codMobile":
              this.codPay();
              break;
          default:
            break;
        }
      } else {
        this.verifyCode().then(res => {
          switch (this.type) {
            case "forget":
              this.$router.push({
                path: "setPwd",
                query: { username: this.username }
              });
              break;
            case "register":
              this.register();
              break;
            default:
              break;
          }
        });
      }
    },
    sendSms() {
      let func = this.accountType == 1 ? sendEmail : sendSms;
      func(this.username, this.type).then(res => {
        this.$toast("Code send successfully");
      });
    },
    verifyCode() {
      return verifyCode({
        codeType: this.type,
        accountType: this.accountType,
        validateCode: this.validateCode,
        account: this.username
      });
    },
    register() {
      let param = JSON.parse(this.$route.query.extra);
      this.$store.dispatch("register", param).then(r => {
        this.$toast(" Sign up successfully");
        //this.$router.go(-2);
        this.$router.push("/login");
      });
    },
    codPay(){
        let param = JSON.parse(this.$route.query.extra);
        param.tel=this.username;
        param.validateCode=this.validateCode;
        codPay(param).then(res=>{
          this.$router.push({path:"paySuccessful",query:{orderNo:param.orderNo}});
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.centerWrap {
  width: 100%;
  padding-top: 10px;
  background: rgba(246, 246, 246, 1);
}
.center {
  width: 100%;
  padding: 0 4%;
  background-color: #fff;
  font-size: 13px;
}
.center p {
  width: 100%;
  padding: 0 6%;
  color: #666666;
  font-size: 13px;
}
.title {
  width: 100%;
  text-align: center;
  display: block;
  font-size: 16px;
  padding: 10px 0 10px 0;
}
.again {
  width: 100%;
  text-align: center;
  display: block;
  font-size: 12px;
  color: #999999;
  padding-top: 10px;
}
.title a {
  font-size: 12px;
  color: #2b87d5;
}
.again a {
  color: #2b87d5;
}
.center .van-cell {
  border-bottom: 1px solid #f4f4f4;
  width: 95%;
  padding: 10px 0;
}
.center .van-button--danger {
  width: 100%;
}
.emailWrap {
  width: 100%;
  padding: 20px 6% 70px 6%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.emailWrap img {
  width: 19px;
  height: 19px;
  margin-right: 5px;
}
</style>



